import React, {useEffect, useState} from 'react';


function createMarkup() {    

    return {__html:   '<iframe src="Squid.html" width="100%" height="1600px"></iframe>'};
  }
function Squid() {
    useEffect(()=>{
        
        
        console.log('안녕');
        
    
    });
    
    let [inputData, inputData변경] = useState('');
    
    return (
    <div>
    { inputData }
    <input onChange={(e)=>{inputData변경(e.target.value)}} />
    <div   style={{margin: 'auto', position: 'relative', width: '100%', height: '100%', overflow: 'hidden' }} dangerouslySetInnerHTML={createMarkup()} />
    </div>
    )
  }
  
export default Squid;